import React, {useState} from 'react';
import Navbar from './Navbar';
import Heading from './Heading';
import Input from './Input';
import Output from './Output';
import Outline from './Outline';
import Boost from './Boost';
import Footer from './Footer';
import { computeHeadingLevel } from '@testing-library/dom';
const http = require("http");

let list = [];



function App() {

const [urlObject, setUrlObject] = useState({
    user_url: "",
    shortenUrl: ""
  });



const urlArrayitem = [urlObject];

const temporaryList = list;

const [inputStyle, setInputStyle] = useState({
 border: "",
});

const [error, setError] = useState({
  display: ""
});

const [className, setClassName] = useState("");


  // const [list, setList] = useState([]);

  // function updateList(item){
  //   setList(
  //     [list,...item]
  //   );
  //   }
    


// function setUserUrl(newInput){
//   const user_url = newInput;
//   getUrl(user_url);
// }




function getUrl(item){

  setUrlObject(
    {
    user_url: "",
    shortenUrl: ""
  });

  const api_url= "https://api.shrtco.de/v2/shorten?url=";

  const fullUrl = api_url + item;
  
  console.log("item " + fullUrl);
  
  http
  .get(fullUrl, function(response){
    response.on("data", function(data){
      const urlData = JSON.parse(data);
      if(urlData.ok === false){
        
        setInputStyle({
          border: "2px solid red",
          textColor: "red"
        });
        setClassName(
          "placeholdercolor"
        );
        setError({
          display: "inline"
        });
        console.log(urlData);
      }else{
    setUrlObject(
      {
      user_url: item,
      shortenUrl: urlData.result.full_short_link
    });
  }
    })

})
.end();
}

console.log("object" + urlObject.user_url);
if((urlObject.user_url !== "") && (urlObject.shortenUrl !== "")){
  list = [...temporaryList, ...urlArrayitem];
} else{
  list = list;
}


console.log(list);
console.log(inputStyle);

  return (
    <div>
  <Navbar/>
  <Heading />
  <Input 
  urlCatch={getUrl}
  style={inputStyle}
  error={error}
  className={className}
    />
    <section id="output">
{list.map((item, index)=>{
      return(
       <Output
         key={index}
         index={index}
         userUrl={item.user_url}
         shortenUrl={item.shortenUrl}
         />
      )
      }
      )
}
</section>
       
  <Outline />
  <Boost />
  
  <Footer />
  
</div>
    
  );
}

export default App;
